/* eslint-disable no-shadow,no-param-reassign */
import axios from '@/service/axios';

const state = {
  paymentRequests: [],
  limit: 10,
  page: 1,
  fullCount: 0,
  totalPages: 1,
};

const getters = {
  firstRecord(state) {
    return (state.page - 1) * state.limit + 1;
  },
  lastRecord(state) {
    return state.page === state.totalPages ? state.fullCount : state.page * state.limit;
  },
};

const mutations = {
  setPaymentRequests(state, newPaymentRequests) {
    state.paymentRequests = [...newPaymentRequests];
  },
  setLimit(state, newValue) {
    state.limit = newValue;
  },
  setPage(state, newValue) {
    state.page = newValue;
  },
  setFullCount(state, newValue) {
    state.fullCount = newValue;
  },
  setTotalPages(state, newValue) {
    state.totalPages = newValue;
  },
};

const actions = {
  async getPaymentRequests({ state, commit }) {
    const {
      data: { results, count, total_pages: totalPages },
    } = await axios.get('/payment-requests/', {
      params: {
        page_size: state.limit,
        page: state.page,
      },
    });
    commit('setPaymentRequests', results);
    commit('setFullCount', count);
    commit('setTotalPages', totalPages);
  },
};

export default {
  state,
  getters,
  mutations,
  actions,
  namespaced: true,
};
